<template>
  <div class="homepage">
    <!-- 顶部导航 -->
    <div class="top_tab">
      <div class="left" @click="back">
        <img class="xzs" src="../../assets/images/commn/back.png">
      </div>
      <span>关于我们</span>
      <div class="right">
      </div>
    </div>
     <!-- 空DIV -->
    <div class="hight-r"></div>
		<div class="top">
			<div class="pic">
				<img :src="info.logoimage" alt="">
			</div>
		</div>
		<div class="box">
			<div class="name">
				<span>平台介绍</span>
			</div>
			<div class="txt">
				<span>
					{{info.content}}
				</span>
			</div>
			<div class="name">
				<span>联系我们</span>
			</div>
			<div class="ad"> 
				<span>地址：{{info.address}}</span>
				<span class="phone">电话：{{info.phone}}</span>
			</div>
		</div>
  </div>
</template>

<script>
export default {
  name: 'index',
  data () {
    return {
			info:''
    }
  },
  methods: {
		back: function() {
			this.$router.go(-1);
    },
		about: function() {
			let self = this;
			let url = "/api/other/my"; 
			self.httpGet(url, function(res) {
				console.log(1111,res)
				if (res.code == 1) {
					self.info = res.data 
				} else {
					self.$dialog.toast({
						mes: res.msg,
						timeout: 1500,
						icon: "error"
					});
				}
			});
		},
  },
  created() {
		this.about();
	}
}
</script>


<style scoped>
.homepage{
	background: #fff;
}
/* 顶部tab */
.top_tab {
  width:100%;
	height:1rem;
  padding:0 .3rem;
  background: #FFF;
  display: flex;
	position: fixed;
  justify-content: space-between;
  align-items: center;
	z-index: 99999;
}
.top_tab span {
  color: #000;
  font-size: .30rem;
	font-weight: bold;
}
.top_tab .left img {
  width: 0.26rem;
	height: 0.35rem;
	margin-top: .1rem;
}
/* 顶部空 */
.hight-r {
  height: 1rem;
}
.top{
	width:100%;
	height:2.47rem;
	background: url(data:image/png;base64,);
	background-size: 100% 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}
.top .pic{
	width:1.40rem;
	height:1.4rem;
	overflow: hidden;
	border-radius: 5px;
}
.top .pic img{
	width:100%;
	height:100%;
}
.box{
	width:100%;
	padding: 0 .30rem;;
	box-shadow:  0 1px 5px rgba(219,219,218,.9);
	display: flex;
	flex-direction: column;
}
.box .name{
	width:100%;
	display: flex;
	align-items: center;
	height:.8rem;
	border-bottom: 1px solid #f2f2f2;
}
.box .name span{
	font-size: .28rem;
}
.box .txt{
	width:100%;
	padding: .2rem 0 0 0;
}
.box .ad{
	width:100%;
	padding: .2rem 0;
	display: flex;
	flex-direction: column;
}
.box .ad .phone{
	margin-top: .1rem;
}
</style>



